import styled from "styled-components";

const Input = styled.input`
  background: white;
  border: none;
  font-size: 16px;
  line-height: 50px;
  height: 50px;
  width: 100%;
  max-width: 300px;
  border-radius: 3px 0px 0px 3px;
  outline: none;
  padding-left: 15px;
`;

export default Input;



