import styled from "styled-components";

const Icon = styled.button.attrs({
  type: "submit"
})`
  border: none;
  background: #7b1fa2;
  color: white;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  padding: 0px 15px;
  border-radius: 0px 3px 3px 0px;
`;

export default Icon;



